import React from 'react'
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';
import contactGroup from "../assets/contactgroups.png"
import contacts from "../assets/contacts.png"
import blacklists from "../assets/s_blacklist.png"
import sms_t from "../assets/sms_template.png"
import subscribe from "../assets/subscribe.png"
import info from "../assets/info.png"
import history1 from "../assets/historybar1.png"
import history2 from "../assets/historybar2.png"
import pinkbar from "../assets/pinkbar1.png"
import pinkbar2 from "../assets/pinkbar2.png"

const Dashboardview = () => {
    const percentage = 50;
const percentages = 0;
    return (
        <div>
             <div className='w-full  mt-[20px] mr-[40px] '>
          <div className='bg-white pt-[18px] pl-[18px] pb-[23.5px] rounded-[2px]'>
            <p className='text-[16px] leading-[25.6px] text-[#5F6368] font-normal pb-[15px]'>Statistics</p>
            <div className='grid grid-cols-4 '>


              <div className='flex items-center gap-[14px] cursor-pointer relative'>
                <div className='w-[106px]'>
                  <p className='text-[21px] leading-[33.6px] text-[#0092D7]  font-normal'>0</p>
                  <p className='text-[14px] leading-[22.4px] font-normal text-[#5F6368]'>Contact Groups</p>
                </div>
                <div className='w-[40px] h-[40px] bg-[#E6F7FF] rounded-full flex items-center justify-center' >
                  <img src={contactGroup} alt="" />
                </div>
                <div className=' absolute top-0 right-[30%] h-[100%] bg-[#EDEDED] w-[1px]'></div>
              </div>
              <div className='flex items-center gap-[14px] cursor-pointer relative'>
                <div className='w-[106px]'>
                  <p className='text-[21px] leading-[33.6px] text-[#24E073]  font-normal'>0</p>
                  <p className='text-[14px] leading-[22.4px] font-normal text-[#5F6368]'>Contacts</p>
                </div>
                <div className='w-[40px] h-[40px] bg-[#E5F8ED] rounded-full flex items-center justify-center' >
                  <img src={contacts} alt="" />
                </div>
                <div className='absolute top-0 right-[30%] h-[100%] bg-[#EDEDED] w-[1px]'></div>
              </div>
              <div className='flex items-center gap-[14px] cursor-pointer relative'>
                <div className='w-[106px]'>
                  <p className='text-[21px] leading-[33.6px] text-[#E12D2D]  font-normal'>0</p>
                  <p className='text-[14px] leading-[22.4px] font-normal text-[#5F6368]'>Blacklist</p>
                </div>
                <div className='w-[40px] h-[40px] bg-[#FCEAEA] rounded-full flex items-center justify-center' >
                  <img src={blacklists} alt="" />
                </div>
                <div className=' absolute top-0 right-[30%] h-[100%] bg-[#EDEDED] w-[1px]'></div>
              </div>
              <div className='flex items-center gap-[14px] cursor-pointer'>
                <div className='w-[106px]'>
                  <p className='text-[21px] leading-[33.6px] text-[#F79236]  font-normal'>0</p>
                  <p className='text-[14px] leading-[22.4px] font-normal text-[#5F6368]'>SMS Template</p>
                </div>
                <div className='w-[40px] h-[40px] bg-[#FFF2E6] rounded-full flex items-center justify-center' >
                  <img src={sms_t} alt="" />
                </div>
              </div>
            </div>
          </div>
          <div className='flex gap-[32px]'>
            <div className='basis-[60%] flex flex-col gap-[30px]'>
              <div className='bg-white mt-[32px] pt-[12px] pl-[18px] rounded-[2px] cursor-pointer '>
                <h2 className='text-[20px] leading-[32px ] font-semibold text-[#0092D7]'>Good Morning, Easy Service App</h2>
                <p className='text-[#5F6368] text-[15px] leading-[24px] font-normal max-w-[639px] pb-[12px]'>consectetur adipiscing elit duis tristique sollicitudin nibh sit amet commodo nulla facilisi nullam vehicul.</p>
              </div>
              <div className='bg-white pl-[17.74px] pt-[18px] h-[172px] relative'>
                <h2 className='text-[#5F6368] text-[16px] leading-[18.75px] font-normal'>SMS Report</h2>
                <div className='flex items-center flex-col absolute top-[50%] left-[50%] transform translate-x-[-50%] translate-y-[-50%] gap-[8.45px]'>
                  <img src={subscribe} alt="" />
                  <p className='text-[#C7C7C7] leading-[16.41px] text-[14px] font-normal'>No report generated</p>
                </div>
              </div>
            </div>
            <div className='bg-white mt-[32px] pt-[18px] pl-[18px] basis-[40%] h-[342px] relative rounded-[2px]'>
              <p className='text-[#5F6368] text-[16px] leading-[18.5px] font-normal'>Current Plan</p>
              <div className='flex items-center flex-col absolute top-[50%] left-[50%] transform translate-x-[-50%] translate-y-[-50%] gap-[8.45px]'>
                <img src={subscribe} alt="" />
                <p className='text-[#C7C7C7] leading-[16.41px] text-[14px] font-normal'>No active Subscriptions</p>
              </div>
              <button className='bg-[#F1602B] cursor-pointer flex items-center gap-[10px] py-[15px] px-[45px] rounded-[5px] absolute left-[50%] transform translate-x-[-50%] bottom-[43px]'>
                <img src={info} alt="" />
                <p className='text-[14px] leading-[16.41px] font-normal text-white '>More Info</p>
              </button>
            </div>
          </div>
          <div className='flex gap-[32px]'>
            <div className='bg-white basis-[60%] h-[345px] pt-[18px] pl-[17.74px] relative'>
              <p className='text-[#5F6368] text-[16px] leading-[18.75px] font-normal'>Outgoing SMS History of this Month</p>
              <img src={history1} className="w-[90%] h-[214px] absolute right-[20px] bottom-[35px]" alt="" />
              <img src={history2} alt="" className="w-[90%] h-[126px] absolute right-[20px] bottom-[35px]" />
            </div>
            <div className='bg-white basis-[40%] mt-[27px] pt-[18px] pl-[18px] relative h-[310px]' >
              <p className='text-[#5F6368] text-[16px] leading-[18.5px] font-normal'>Contact Groups</p>

              <div style={{ width: 118, height: 118 }} className="absolute top-[48%] left-[50%] transform translate-x-[-50%] translate-y-[-50%]">

                <CircularProgressbar
                  value={percentage}
                  strokeWidth={3}
                  text={`${percentage}%`}
                  styles={buildStyles({
                    // Rotation of path and trail, in number of turns (0-1)
                  

                    // Whether to use rounded or flat corners on the ends - can use 'butt' or 'round'
                    strokeLinecap: 'butt',

                    // Text size
                    textSize: '35px',

                    // How long animation takes to go from one percentage to another, in seconds
                    pathTransitionDuration: 0.5,

                    // Can specify path transition in more detail, or remove it entirely
                    // pathTransition: 'none',

                    // Colors
                    pathColor: `#0092D7`,
                    textColor: '#003E80',
                    trailColor: '#EDEDED',
                    backgroundColor: '',
                  })}
                />
              </div>
              <div className='flex items-center justify-between border-t-[1px] border-[#EDEDED] pt-[11px] pl-[30px]  absolute top-[74%] w-full'>
                <div className='text-center'>
                  <p className='text-[#AEAEAE] text-[13px]  leading-[15.23px] font-bold'>TOTAL</p>
                  <h2 className='text-[#5F6368] text-[29.33px] leading-[34.38px] font-normal mt-[10px]'>18000</h2>
                </div>
                <div className='pr-[46px] text-center'>
                  <p  className='text-[#AEAEAE] text-[13px]  leading-[15.23px] font-bold'>REMAINING</p>
                  <h2 className='text-[#5F6368] text-[29.33px] leading-[34.38px] font-normal mt-[10px]'>
                  9000
                  </h2>
                </div>
              </div>
            </div>
          </div>
          <div className='flex gap-[32px] pb-[35px]'>
            <div className='bg-white basis-[60%] mt-[32px] pt-[18px] pl-[18px] h-[393px] relative'>
              <p  className='text-[#5F6368] text-[16px] leading-[18.75px] font-normal'>Incoming SMS History of this Month</p>
              <img src={pinkbar} className="w-[90%] h-[214px] absolute right-[20px] bottom-[35px]" alt="" />
              <img src={pinkbar2} alt="" className="w-[90%] h-[126px] absolute right-[20px] bottom-[35px]" />
            </div>
            <div className='bg-white basis-[40%] mt-[27px] pt-[18px] pl-[18px] relative h-[310px]' >
              <p className='text-[#5F6368] text-[16px] leading-[18.5px] font-normal'>Contact Groups</p>

              <div style={{ width: 118, height: 118 }} className="absolute top-[48%] left-[50%] transform translate-x-[-50%] translate-y-[-50%]">

                <CircularProgressbar
                  value={percentages}
                  strokeWidth={3}
                  text={`${percentages}%`}
                  styles={buildStyles({
                    // Rotation of path and trail, in number of turns (0-1)
                  

                    // Whether to use rounded or flat corners on the ends - can use 'butt' or 'round'
                    strokeLinecap: 'butt',

                    // Text size
                    textSize: '35px',

                    // How long animation takes to go from one percentage to another, in seconds
                    pathTransitionDuration: 0.5,

                    // Can specify path transition in more detail, or remove it entirely
                    // pathTransition: 'none',

                    // Colors
                    pathColor: `#0092D7`,
                    textColor: '#003E80',
                    trailColor: '#EDEDED',
                    backgroundColor: '',
                  })}
                />
              </div>
              <div className='flex items-center justify-between border-t-[1px] border-[#EDEDED] pt-[11px] pl-[30px]  absolute top-[74%] w-full'>
                <div className='text-center'>
                  <p className='text-[#AEAEAE] text-[13px]  leading-[15.23px] font-bold'>TOTAL</p>
                  <h2 className='text-[#5F6368] text-[29.33px] leading-[34.38px] font-normal mt-[10px]'>Unlimited</h2>
                </div>
                <div className='pr-[46px] text-center'>
                  <p  className='text-[#AEAEAE] text-[13px]  leading-[15.23px] font-bold'>REMAINING</p>
                  <h2 className='text-[#5F6368] text-[29.33px] leading-[34.38px] font-normal mt-[10px]'>
                  Unlimited
                  </h2>
                </div>
              </div>
            </div>
          </div>
        </div>
        </div>
    )
}

export default Dashboardview