import React, { useState } from 'react'
import logo from "../assets/logo.png"
import lang from "../assets/language.png"
import notification from "../assets/notification.png"
import profile from "../assets/profile.png"
import arrow from "../assets/arrowdown.png"
import online from "../assets/online.png"
import dashboard from "../assets/dashboard.png"
import reports from "../assets/reports.png"
import contact from "../assets/contact.png"
import blacklist from "../assets/blacklist.png"
import chatbox from "../assets/chatbox.png"
import sending from "../assets/sending.png"
import arrow_right from "../assets/arrow_right.png"
import profile2 from "../assets/profile2.png"
import biling from "../assets/billing.png"
import something from "../assets/something.png"
import logout from "../assets/logout.png"
import 'react-circular-progressbar/dist/styles.css';
import dashboardB from "../assets/dashboardblack.png"
import { Outlet, Link, NavLink } from "react-router-dom"


const Dashboard = () => {
  const [show, setShow] = useState(false)
  const [dropdown, setDropdown] = useState(false)
  const [active, setActive] = useState(false)
  const [sendings, setSendings] = useState(false)
  const [smss, setSmss] = useState(false)

  const showProfile = () => {
    setShow(!show)
  }
  const showDropDown = () => {
    setDropdown(!dropdown)
    setSendings(false)
    setSmss(false)


  }
  const sendingg = () => {
    setSendings(!sendings)
    setDropdown(false)
    setSmss(false)

  }
  const opensms = () => {
    setSmss(!smss)
    setDropdown(false)
    setSendings(false)
  }


  return (
    <div className='bg-[#F9F9F9]'>
      {/* topbar */}
      <div className='pl-[55px] pt-[10px] pb-[8px] border bg-white border-b-[#D9D9D9] flex items-center justify-between pr-[40px] sticky z-10 top-0 w-full'>
        <NavLink to="/">
          <img src={logo} alt="" className='cursor-pointer' />
        </NavLink>
        <div className='flex items-center gap-[24px]'>
          <img src={lang} alt="" className='w-[16px] h-[16px] cursor-pointer' />
          <img src={notification} alt="" className='w-[14px] h-[18px] cursor-pointer' />
          <div className='flex items-center relative cursor-pointer' onClick={showProfile}>
            <div className='relative cursor-pointer'>
              <img src={profile} alt="" className='mr-[12px] w-[32px] h-[32px] cursor-pointer' />
              <img src={online} alt="" className='absolute bottom-[1px] right-[13px]' />
            </div>
            <p className='mr-[7px] text-[12px] leading-[14.06px] font-medium text-[#5F6368] cursor-pointer'>John Doe Jr.</p>
            <img src={arrow} alt="" />

            {
              show &&
              <div className='absolute top-[50px] right-[-2px] bg-white pl-[16px] py-[13px] w-[144px] flex flex-col gap-[16px] border border-[#EDEDED] rounded-[2px]'>
                <div className='flex items-center gap-[6px] cursor-pointer'>
                  <img src={profile2} alt="" />
                  <p className='text-[#5F6368] text-[12px] leading-[14.52px] font-normal'>Profile</p>
                </div>
                <div className='flex items-center gap-[6px] cursor-pointer'>
                  <img src={biling} alt="" />
                  <p className='text-[#5F6368] text-[12px] leading-[14.52px] font-normal'>Billing</p>
                </div>
                <div className='flex items-center gap-[6px] cursor-pointer'>
                  <img src={something} alt="" />
                  <p className='text-[#5F6368] text-[12px] leading-[14.52px] font-normal'>Something </p>
                </div>
                <div className='flex items-center gap-[6px] cursor-pointer'>
                  <img src={logout} alt="" />
                  <p className='text-[#F1592A] text-[12px] leading-[14.52px] font-normal'>Logout</p>
                </div>
              </div>
            }
          </div>
        </div>
      </div>
      <div className=' flex gap-[20px]'>
        {/* sidebar */}
        <div className=' bg-white  flex px-[16px] pt-[20px] flex-col gap-[12px] h-screen fixed  basis-[15%]'>
          <div className='flex items-center gap-[16px] nav_link rounded-[6px] pl-[18px] py-[16px] max-w-[223px] cursor-pointer group'>
            <img src={dashboardB} alt="" className='group-hover:invert' />
            <NavLink to="/">
              <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Dashboard</p></NavLink>
          </div>
          <div className='flex items-center flex-col gap-[16px]  rounded-[6px]    w-[223px] cursor-pointer '>
            <NavLink to="#" className="w-full" style={({ isActive }) => { return { backgroundColor: isActive ? "" : "" } }} >
              <div className="flex items-center gap-[16px] py-[16px] nav_link  w-full pl-[18px] rounded-[6px] group" onClick={showDropDown}>
                <div className='flex items-center gap-[16px]'>
                  <img src={reports} alt="" className='group-hover:invert' />
                  <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Reports</p>
                </div>
                <img src={arrow_right} alt="" className='group-hover:invert' />
              </div>
            </NavLink>
            {
              dropdown &&
              <div className='w-full transition duration-300 ease-out flex flex-col gap-[12px]'>
                <div className='flex w-full items-center gap-[16px] pl-[18px] py-[16px] group nav_link  rounded-[6px]'>
                  <img src={contact} alt="" className='group-hover:invert' />
                  <NavLink to="/contact" >
                    <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>All messages</p></NavLink>
                </div>
                <div className='flex w-full items-center gap-[16px] pl-[18px] py-[16px] group nav_link  rounded-[6px]'>
                  <img src={contact} alt="" className='group-hover:invert' />
                  <NavLink to="/contact">
                    <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Inbox</p></NavLink>
                </div>
                <div className='flex w-full items-center gap-[16px] pl-[18px] py-[16px] group nav_link  rounded-[6px]'>
                  <img src={contact} alt="" className='group-hover:invert' />
                  <NavLink to="/contact">
                    <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Sent</p></NavLink>
                </div>
                <div className='flex w-full items-center gap-[16px] pl-[18px] py-[16px] group nav_link  rounded-[6px]'>
                  <img src={contact} alt="" className='group-hover:invert' />
                  <NavLink to="/contact">
                    <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Campaigns</p></NavLink>
                </div>
              </div>
            }

          </div>
          <NavLink to="/contact" style={({ isActive }) => { return { backgroundColor: isActive ? "" : "" } }} >
            <div className='flex items-center  rounded-[6px] pl-[18px] py-[16px]  nav_link w-[223px] cursor-pointer group'>
              <div className='flex w-full items-center gap-[16px]'>
                <img src={contact} alt="" className='group-hover:invert' />
                <Link to="/contact">
                  <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Contact Groups </p></Link>
              </div>
            </div>
          </NavLink>
          <NavLink to="" style={({ isActive }) => { return { backgroundColor: isActive ? "" : "" } }} >
            <div className='flex items-center flex-col gap-[16px] rounded-[6px]   w-[223px] cursor-pointer '>
              <div className='flex items-center gap-[16px] py-[16px]  nav_link w-full pl-[18px] rounded-[6px] group' onClick={sendingg}>
                <div className='flex items-center gap-[16px]'>
                  <img src={sending} alt="" className='group-hover:invert' />
                  <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Sending</p>
                </div>
                <img src={arrow_right} alt="" className='group-hover:invert' />
              </div>

              {
                sendings &&
                <div className='w-full transition duration-300 ease-out flex flex-col gap-[12px]'>
                  <div className='flex w-full items-center gap-[16px] pl-[18px] py-[16px] group nav_link  rounded-[6px]'>
                    <img src={contact} alt="" className='group-hover:invert' />
                    <NavLink to="/contact" >
                      <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Sender ID</p></NavLink>
                  </div>
                  <div className='flex w-full items-center gap-[16px] pl-[18px] py-[16px] group nav_link  rounded-[6px]'>
                    <img src={contact} alt="" className='group-hover:invert' />
                    <NavLink to="/contact">
                      <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Numbers</p></NavLink>
                  </div>
                  <div className='flex w-full items-center gap-[16px] pl-[18px] py-[16px] group nav_link  rounded-[6px]'>
                    <img src={contact} alt="" className='group-hover:invert' />
                    <NavLink to="/contact">
                      <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>SMS Templates</p></NavLink>
                  </div>

                </div>
              }


            </div>
          </NavLink>
          <div className='flex items-center  rounded-[6px] pl-[18px] py-[16px] nav_link w-[223px] cursor-pointer group'>
            <div className='flex items-center gap-[16px]'>
              <img src={blacklist} alt="" className='group-hover:invert' />
              <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Blacklist</p>
            </div>
          </div>
          <div className='flex items-center flex-col gap-[16px] rounded-[6px]   w-[223px] cursor-pointer ' >
            <div className='flex items-center gap-[16px] py-[16px]  nav_link w-full pl-[18px] rounded-[6px] group' onClick={opensms}>
              <div className='flex items-center gap-[16px]'>
                <img src={contact} alt="" className='group-hover:invert' />
                <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>SMS</p>
              </div>
              <img src={arrow_right} alt="" className='group-hover:invert' />
            </div>

            {
              smss &&
              <div className='w-full transition duration-300 ease-out flex flex-col gap-[12px]'>
                <div className='flex w-full items-center gap-[16px] pl-[18px] py-[16px] group nav_link  rounded-[6px]'>
                  <img src={contact} alt="" className='group-hover:invert' />
                  <NavLink to="/contact" >
                    <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Campaign Builder</p></NavLink>
                </div>
                <div className='flex w-full items-center gap-[16px] pl-[18px] py-[16px] group nav_link  rounded-[6px]'>
                  <img src={contact} alt="" className='group-hover:invert' />
                  <NavLink to="/contact">
                    <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Quick send</p></NavLink>
                </div>
                <div className='flex w-full items-center gap-[16px] pl-[18px] py-[16px] group nav_link  rounded-[6px]'>
                  <img src={contact} alt="" className='group-hover:invert' />
                  <NavLink to="/contact">
                    <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Send using File</p></NavLink>
                </div>
              </div>
            }



          </div>
          <div className='flex items-center  rounded-[6px] pl-[18px] py-[16px] nav_link w-[223px] cursor-pointer group'>
            <div className='flex items-center gap-[16px]'>
              <img src={chatbox} alt="" className='group-hover:invert' />
              <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Chat Box</p>
            </div>
          </div>
          <div className='flex items-center  rounded-[6px] pl-[18px] py-[16px] nav_link w-[223px] cursor-pointer group'>
            <div className='flex items-center gap-[16px]'>
              <img src={chatbox} alt="" className='group-hover:invert' />
              <p className='text-[#5F6368] text-[14px] leading-[16.41px] font-normal group-hover:text-[#F5F5F5] w-[130px] '>Developers</p>
            </div>
          </div>
        </div>
        {/* right section */}
        <div className='ml-[280px] basis-[85%] mr-[40px]'>
          <Outlet></Outlet>
        </div>
      </div>
    </div>
  )
}

export default Dashboard