import React from 'react'
import logo from "../assets/biglogo.png"

const Login = () => {
    return (
        <div className=' bg-[#F9F9F9]'>
            <div className='grid grid-cols-2 h-screen  py-[134px]'>
                <div className='flex items-center justify-center border-r-[1px]'>
                    <img src={logo} alt="" className='w-[246px] h-[100.55px]' />
                </div>
                <div className='  flex items-center justify-center flex-col px-[166px] 2xl:px-[186px]  '>

                    <div className='w-full'>
                        <h2 className='text-[#0063A5] text-[22px] leading-[28.13px] font-normal text-center'>Sign In</h2>
                        <p className=' mt-[41px] text-[14px] leading-[16.41px] text-[#545454] w-full  font-normal'>Please sign-in to your account and start the
                            adventure</p>
                        <form action="" className='w-full'>
                            <div className='w-full mt-[31px]'>
                                <p className='text-[13px] leading-[15.23px] font-normal text-[#545454]'>Email</p>
                                <input type="email" placeholder='Email address' className='w-full outline-none border border-[#D9D9D9] rounded-[1px] pt-[11px] pl-[14px] pb-[12px] mt-[4px] placeholder:text-[13px] placeholder:text-[#000] placeholder:opacity-[0.5] placeholder:font-normal placeholder:leading-[15.23px] text-[13px]' />
                            </div>
                            <div>
                                <div className='flex items-center justify-between mt-[24px] mb-[5px]'>
                                    <p className='text-[13px] leading-[15.23px] font-normal text-[#545454]'>Password</p>
                                    <p className='text-[#0072B5] text-[13px] leading-[15.23px] font-normal cursor-pointer'>Forgot Password?</p>
                                </div>
                                <input type="password" placeholder='Password' className='w-full outline-none border border-[#D9D9D9] rounded-[1px] pt-[11px] pl-[14px] pb-[12px] mt-[4px] placeholder:text-[13px] placeholder:text-[#000] placeholder:opacity-[0.5] placeholder:font-normal placeholder:leading-[15.23px] text-[13px]' />
                            </div>
                            <div className='flex items-center gap-[8px] mt-[24px]'>
                                <input type="checkbox" name="" id="" className='w-[16px] h-[16px] cursor-pointer border border-[#808080] outline-none' />
                                <p className='text-[13px] leading-[15.23px] font-normal text-[#545454]'>Remember Me</p>
                            </div>
                            <button className='btn w-full text-[14px] leading-[16.41px] font-normal text-[#F9F9F9] py-[14px] pl-[133px] pr-[134px] mt-[24px]'>
                                Login
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Login